import React from "react";
import Post from "./Post";
import imageCode from "../assets/imageCode.jpg";
import avt from "../assets/avatar.svg";
import { connect } from "react-redux";
import { useHistory } from "react-router-dom";

const Posts = ({ posts, themeIsLight }) => {
  const history = useHistory();
  return (
    <main className='section posts'>
      <div className={`post ${themeIsLight ? "" : "post--isDark"}`}>
        <div className='post__image'>
          <img src={`${imageCode}`} alt='screenshot' />
        </div>
        <div className='post__title'>
          <h4>Nice post</h4>
        </div>
        <div className='post__author'>
          <div className='post__authorLeft'>
            <img src={`${avt}`} alt='user avatar' />
          </div>
          <div className='post__authorRight'>
            <p>msT Lucius</p>
            <p className='post__date'>21 January 2021</p>
          </div>
        </div>
        <div className='post__intro'>
          <p>Intro</p>
        </div>
        <div className='post__tags'>
          {["react", "redux", "javascript"].map((tag) => (
            <div
              key={tag}
              className={`post__tag ${themeIsLight ? "" : "post__tag--isDark"}`}
            >
              <p>{tag}</p>
            </div>
          ))}
        </div>
      </div>
      {posts.map(
        ({ id, image, title, date, author, avatar, intro, content, tags }) => {
          return (
            <Post
              id={id}
              key={id}
              image={image}
              title={title}
              date={date}
              author={author}
              avatar={avatar}
              intro={intro}
              content={content}
              tags={tags}
            />
          );
        }
      )}
    </main>
  );
};

const mapStateToProps = (state) => ({
  themeIsLight: state.global.themeIsLight,
});

export default connect(mapStateToProps)(Posts);
